import React from 'react'
import Card from '../components/Card'
import Intro from '../components/Intro'
import Feature from '../components/Feature'

const Layout = (props) => (
    <>
        <h1 style={{ textAlign: 'center'}}>{props.title}</h1>
        
        <div className="row" style={{marginTop: 40, marginBottom: 100}}>
            <Intro content={props.intro.content}/>

            <div className="col-md-6" style={ props.layoutStatus === 'devSchool' ? styles.borderNone : styles.box450border  } >
                {
                        props.features.map(feature => (
                            
                            props.layoutStatus === 'devSchool' ?
                                <img 
                                    src={feature.img}
                                    alt=""
                                />
                            :
                                <Feature 
                                    title={feature.title} 
                                    desc={feature.desc} 
                                />
                        ))
                }
            </div>
        </div>

        <div className="row" style={{ marginBottom: 50 }}>
            {
                props.cards.map(card => 
                    <Card 
                        imageUrl={card.imageUrl} 
                        title={card.title} 
                    />
                )
            }
        </div>
    </>
);


const styles = {
    box450border: {
        height: '450px',
        border: '2px solid #f1f1f1',
        borderRadius: 10,
        padding: 30
    },
    boxNone:{
        border:'none'
    }
}


export default Layout